@charset "utf-8";


@import "base";
@import "values";
@import "architecture";
@import "legacy";
@import "font";
@import "platform";

section {
	margin-bottom: 0;
}

.header-container {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	z-index: 15;
	

	.header {
		display: flex;
		justify-content: space-between;

		transition: all 0.3s ease;
		background-color: white;
		padding: 20px 25px;
		box-shadow: 0 0 60px rgba(0, 0, 0, 0.15);

		@media all and (max-width: $mobile) {
			padding: 10px;
		}

		&.stuck {}
	}

	&.top {
		position: relative;
		

		.header {
			background-color: transparent;
			box-shadow: none;

			.logo {
				.desktop-logo {
					display: none;
				}

				.desktop-logo-white {
					display: block;

					@media all and (max-width: $mobile) {
						display: none;
					}
				}
			}

			

			.language-switcher {


				.links {
					padding: 0;
					border: 2px solid rgba(255, 255, 255, 0.2);

					a {
						color: white;

						&:hover {
							color: $orange;
						}
					}


					a,
					span {
						&:first-child {
							padding-left: 12px;

							@media all and (max-width: $mobile) {
								padding-left: 12px;
							}
						}

						&:last-child {
							padding-right: 12px;

							@media all and (max-width: $mobile) {
								padding-right: 12px;
							}

						}
					}


				}


				span {
					color: gray;
				}


			}


		}
	}

	&.dark {
		position: relative;

		.header {
			background-color: transparent;

			.logo {
				.desktop-logo {
					display: none;
				}

				.desktop-logo-white {
					display: block;

					@media all and (max-width: $mobile) {
						display: none;
					}
				}
			}

			.tags-navigation {

				a,
				span {
					color: white;
					background-color: transparent;
					padding: 9px 20px 10px 16px;
					border: 2px solid $half-transparent-orange;
					border-right: none;

					@media all and (max-width: $mobile) {
						padding: 4px 9px 3px 9px;
					}

					&:first-child {
						// border-right: none;
						padding-left: 16px;
					}

					&:last-child {
						border-right: 2px solid $half-transparent-orange;
						padding-right: 16px;
					}



					&:hover {
						color: $orange;
					}
				}
			}

			.language-switcher {


				.links {
					padding: 0;
					border: 2px solid rgba(255, 255, 255, 0.2);

					a {
						color: white;

						&:hover {
							color: $orange;
						}
					}


					a,
					span {
						&:first-child {
							padding-left: 12px;

							@media all and (max-width: $mobile) {
								padding-left: 12px;
							}
						}

						&:last-child {
							padding-right: 12px;

							@media all and (max-width: $mobile) {
								padding-right: 12px;
							}

						}
					}


				}


				span {
					color: gray;
				}


			}


		}
	}
}



.tags-navigation {
	margin-right: 20px;
	border: 1px solid $half-transparent-orange;
	border-radius: 4px;
	overflow: hidden;
	display: flex;
	flex-direction: row;

	@media all and (max-width: $mobile) {
		font-size: 14px;
		margin-right: 10px;
	}

	a, button {
		display: flex;
		position: relative;
		box-sizing: border-box;
		z-index: 1;
		cursor: pointer;

		padding: 9px 20px 10px 20px;
		margin: 0;
		
		border: none;
		color: $orange;
		background: transparent;

		font-family: $main-font;
		font-size: 16px;
		line-height: 19px;


		@media all and (max-width: $mobile) {
			font-size: 1em;
			line-height: 1.7;
			padding: 3px 7px 4px 9px;
		}

		svg {
			margin-left: 3px;
			margin-top: 7px;
			width: 11px;
			transform: rotate(0deg);

			@media all and (max-width: $mobile) {
				margin-top: 10px;
				margin-left: 4px;
				width: 9px;
			}

			&.rotated {
				transform: rotate(180deg);
			}

			polyline {
				fill: none;
				stroke: #ff6900;
				stroke-miterlimit: 10;
				stroke-width: 1px;
			}
		}
	}

	.link {
		position: static;

		@media all and (max-width: $mobile) {
			&:first-of-type { display: none;}

			span {
				max-width: 100px;
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
			}

			&.last {
				span {
					max-width: 103px;
					overflow: hidden;
					white-space: nowrap;
					text-overflow: ellipsis;
				}
			}
		}


		&:first-of-type .arrow-button:before, &:first-of-type .arrow-button:after {
			width: calc(100% + 20px);
		}

		&.last .arrow-button:before, &.last .arrow-button:after {
			left: -1px;
			width: calc(100% + 20px);

			@media all and (max-width: $mobile) {
				left: -1px;
			}
		}

	}

	.arrow-button {
		&.start {
			z-index: 3;
		}
		
		&.section-divide {
			z-index: 2;
		}

		&.article {
			z-index: 1;
		}

		&.active:before, &.active:after {
			background: #fff0e7;
		}
	}
	

	.nav-popup {
		position: absolute;
		display: none;

		margin-top: 5px;
		background-color: white;
		// border: 1px solid $half-transparent-orange;
		border-radius: 4px;
		box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);

		a {
			display: block;

			&:hover {
				background-color: $transparent-orange;
			}
		}

		.arrow-button:before, .arrow-button:after {
			display: none;
		}
	}

	.link:hover > .arrow-button:before, .link:hover > .arrow-button:after {
		background: #fff0e7;
	}

	.arrow-button:before, .arrow-button:after {
		position: absolute;
		content: "";
		width: 100%;
		height: 57%;
		right: 0;
		z-index: -1;
		box-sizing: border-box;

		border-radius: 0 3px 3px 0;
		border-right: 1px solid #ffc3a4;

		@media all and (max-width: $mobile) {
			width: calc(100% + 20px);
		}
	}

	.arrow-button:before {
		top: -1px;
		transform: skewX(25deg);
	}

	.arrow-button:after {
		top: 47%;
		transform: skewX(-25deg);
	}

}

.header-caption {
	margin: 11px 9px;

	font-size: 16px;
	line-height: 19px;
}

.headers-navigation {

	@media all and (max-width: $mobile) {
		display: none;
	}

	@media screen and (max-width: 1180px) {
		display: none;
	}

	a {
		display: inline-block;
		margin: 11px 9px;

		font-size: 16px;
		line-height: 19px;

		@media all and (max-width: $mobile) {
			margin-right: 10px;
			margin-bottom: 3px;
			font-size: 10px;
			line-height: 12px;
		}

		cursor: pointer;

		color: $orange;
		text-decoration: none;
		border-bottom: 1px dotted $half-transparent-orange;

		&:hover {
			background-color: $supertransparent-orange;

		}
	}
}

.logo {
	width: 120px;
	margin-right: 35px;

	@media all and (max-width: $mobile) {
		top: 10px;
		left: 10px;
		width: 30px;
		margin-right: 10px;
	}

	.desktop-logo-white {
		display: none;
	}

	.desktop-logo {
		@media all and (max-width: $mobile) {
			display: none;
		}
	}

	.mobile-logo {
		display: none;

		@media all and (max-width: $mobile) {
			display: block;
		}
	}

	img {
		display: block;
		width: 100%;
	}

	a {
		border-bottom: 0;

		&:hover {
			opacity: .9;
		}
	}
}

.language-switcher {
	margin-left: auto;

	@media all and (max-width: $mobile) {
		top: 10px;
		right: 10px;
		transform-origin: top right;
	}

	.links {
		padding: 0;

		@media all and (max-width: $mobile) {
			font-size: 0.9em;
			border: 1px solid rgba(0, 0, 0, 0.2);
			border-radius: 4px;
		}

		a,
		span {
			padding: 6px 5px 6px;

			&:first-child {
				padding-left: 12px;

				@media all and (max-width: $mobile) {
					padding-left: 12px;
				}
			}

			&:last-child {
				padding-right: 12px;

				@media all and (max-width: $mobile) {
					padding-right: 12px;
				}

			}
		}


	}


	span {
		color: gray;
	}


}

.main-cover {
	position: relative;
	padding: 0 0 3em;
	background-color: black;
	color: white;

	@media all and (max-width: $mobile) {
		padding: 0 0 0.5em;
	}

	.main-cover-section {
		position: relative;
		margin: 0 auto;
		width: 94%;
		max-width: 1300px;

		@media all and (max-width: $mobile) {
			margin: 0 auto 18px;
			width: 90%;
		}

	}

	h1 {
		margin-top: 0;
		// font-family: 'PT_Root_UI_Light';
		font-size: 5.5em;
		line-height: .95em;
		color: white;
		font-weight: 400;

		@media all and (max-width: $mobile) {
			margin-top: 0em;
			font-size: 2.8em;
			line-height: .95em;
			
		}
	}

	a {
		color: white;
		text-decoration: none;
		border-bottom: 1px solid rgba(255, 255, 255, .3);

		&:hover {
			color: $link-hover-colour;
			border-color: $link-hover-underline-colour;
		}
	}

	.cover-background-image {
		position: absolute;
		display: flex;
		align-items: center;
		justify-content: center;
		top: 0;
		bottom: 0;
		width: 100%;
		z-index: 0;
		overflow: hidden;
		background-image: url("../assets/images/main-cover-images/archer.jpg");
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;

		opacity: 0;
		transition: opacity .4s ease;

		&.active {
			opacity: 1;
		}


		video {
			position: absolute;
			top: 0;
			bottom: 0;
			width: 100%;
		}
		

		&.default,
		&[name="brand"], 
		&[name="design"],
		&[name="customers"],
		&[name="policy"] {
			background-image: url("../assets/images/main-cover-images/archer.jpg");
			background-size: cover;
			background-position: center;

			@media all and (max-width: $mobile) {
				background-size: 115%;
				background-position: center 27%;
			}
		}



		&[name="values"] {
			background-image: url("../assets/images/main-cover-images/values3.png");
			
			background-size: 90%;
			background-position: center 50%;

			&.active {
				opacity: .9;
			}
		}

		&[name="audience"] {
			background-image: url("../assets/images/main-cover-images/people.png");
			background-size: 85%;
			background-position: center 60%;

			&.active {
				opacity: .8;
			}
			
		}

		&[name="culture"] {
			background-image: url("../assets/images/main-cover-images/corirer.png");
			background-size: 20em auto;
			background-position: center 90%;
		}

		&[name="text-style-guide"] {
			background-image: url("../assets/images/main-cover-images/rules.svg");
			background-size: 85%;
			background-position: center 60%;
		}


		&[name="logo"] {
			background-image: url("../assets/images/main-cover-images/logo-upd.png");
			background-size: 26em auto;
			background-position: center 50%;
		}


		&[name="colors"] {
			background-image: url("../assets/images/main-cover-images/color-cloud.png");
			background-size: 95%;
			
			background-position: center 70%;
		}

		&[name="advertising"] {
			background-image: url("../assets/images/main-cover-images/mockups-2.jpg");
			background-size: 90%;

			&.active {
				opacity: .8;
			}
		}

		&[name="illustrations"] {
			background-image: url("../assets/images/main-cover-images/illustration.svg");
			background-size: 80%;
			background-position: center 80%;
			&.active {
				opacity: .8;
			}
		}

		&[name="facades"] {
			background-image: url("../assets/images/main-cover-images/facade.png");
			background-size: contain;
			background-position: bottom;
		}

		&[name="interiors"] {
			background-image: url("../assets/images/main-cover-images/37097881926_3a4ba0e7ba_k.jpg");
		}

		


	}

	

	.cover-menu {
		margin-top: 6em;
		margin-bottom: 1em;
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-start;
		
		@media all and (max-width: $mobile) {
			margin-top: 10em;
			justify-content: space-between;
		}

		.cover-intro {
			width: 90%;
			@media all and (max-width: $mobile) {
				width: 100%;
			}
		}


		.menu-section {
			width: calc(25% - 20px);
			margin-left: 20px;

			@media all and (max-width: $mobile) {
				width: calc(50% - 10px);
				flex-grow: 1;
				margin-left: 0;
				margin-right: 10px;
				margin-bottom: 20px;
			}

			&:first-of-type {
				width: 25%;
				margin-left: 0;
				
				p {
					margin-top: 0;
					line-height: 1.4em;
				}
				
				@media all and (max-width: $mobile) {
					width: calc(50% - 10px);

					p {
						margin-top: 6em;
					}
				}
			}

			&:nth-of-type(2) {
				@media all and (max-width: $mobile) {
					margin-right: 0;
				}
			}

			&:last-of-type {
				@media all and (max-width: $mobile) {
					margin-right: 0;
					margin-bottom: 0;
				}
			}

			a {
				display: block;
				padding: .5em 0;
				border-bottom: 0;
				border-top: 1px solid rgba(255, 255, 255, .3);

				&:first-of-type {
					margin-top: -10px;
					border-top: none;
					color: $orange;
				}

				&:hover {
					color: white;
					background-color: $orange;
				}
			}
		}
	}


}

/// M A I N  P A G E  L E G A C Y

.title-image {
	max-height: 900px;
	padding-top: 220px;
	background-color: black;

	@media all and (max-width: $mobile) {
		padding-top: 180px;
	}

	img {
		display: block;
		width: 100%;
	}
}

.above {
	position: absolute;
	top: 80px;
	width: 100%;

	@media all and (max-width: $mobile) {
		top: 65px;
	}

	h1 {
		color: white;
		transform-origin: top left;
		transform: scale(.9);

		@media all and (max-width: $mobile) {
			transform: scale(1);
		}
	}

}

.black-back {
	position: relative;
	padding: 1px 0 6% 0;
	margin-top: -20px;
	background-color: black;
	color: white;

	.section {
		margin-top: -100px;
		padding-right: 20%;
		box-sizing: border-box;

		@media all and (max-width: $mobile) {
			margin-top: 0px;
			padding-right: 10%;
		}
	}

	a {
		color: white;
		text-decoration: none;
		border-bottom: 1px solid rgba(255, 255, 255, .3);

		&:hover {
			color: $link-hover-colour;
			border-bottom-color: $link-hover-underline-colour;
		}
	}

}

.detailed-nav {
	margin: 3% 0;
	display: flex;
	flex-wrap: wrap;

	@media all and (max-width: $mobile) {
		margin: 5% 0 10% 0;
	}

	a {
		position: relative;
		width: 23%;
		color: white;
		border: 1px solid rgba(255, 255, 255, .4);
		margin-right: 2%;
		border-radius: 5px;
		padding: 20px 20px 30px 20px;
		box-sizing: border-box;

		@media all and (max-width: $mobile) {
			width: 100%;
			padding: 10px 10px 15px 12px;
			margin-bottom: 3%;
		}

		&:hover {
			color: $orange;
			border-color: $half-transparent-orange;
		}

		h4 {
			word-break: break-all;
			margin-top: 0;

			@media all and (max-width: $mobile) {
				margin-bottom: 0px;
			}
		}

		p {
			display: inline-block;
			margin-right: 10px;
			font-size: .8em;
			line-height: 1em;
		}
	}
}



/// T A B L E S

.table {
	margin-top: 20px;
	font-size: 18px;
	line-height: 24px;

	@media all and (max-width: $mobile) {
		font-size: 15px;
		line-height: 20px;
	}

	.big-letter {
		font-size: 150px;
		line-height: 148px;
		margin-left: -5px;
	}

	.tr {
		border-top: 1px solid rgba(0, 0, 0, 0.1);
		padding-top: 20px;
		padding-bottom: 30px;
		display: flex;

		&.table-header {
			border-top: none;
			padding-bottom: 5px;
			color: rgba(0, 0, 0, 0.6);

			@media all and (max-width: $mobile) {
				display: none;

			}
		}

		@media all and (max-width: $mobile) {
			flex-wrap: wrap;
		}

		.td {
			width: 33%;
			padding-right: 2%;
			box-sizing: border-box;

			@media all and (max-width: $mobile) {
				width: 100%;
				padding-right: 0%;
			}

			img {
				display: block;
				width: 100%;
				height: 100%;
			}

			p {
				@media all and (max-width: $mobile) {
					margin-top: 7px;
				}
			}

		}

	}
}


.negative-samples {
	margin-top: 40px;

	img {
		display: inline-block;
		width: 29%;

		margin-right: 3%;
	}
}


.values-table {
	margin-top: 40px;
	font-size: 16px;
	line-height: 22px;

	.tr {
		@media all and (max-width: $mobile) {
			margin-bottom: 40px;
		}

		.td {
			.mobile {
				margin-top: 40px;
			}

			&:nth-child(1) {
				width: 14%;

				@media all and (max-width: $mobile) {
					width: 100%;
					padding-right: 0%;

					text-align: center;
					padding-bottom: 30px;
					font-size: 30px;
					line-height: 40px;
				}
			}

			&:nth-child(2) {
				width: 23%;

				@media all and (max-width: $mobile) {
					width: 100%;
					padding-right: 0%;
				}
			}

			&:nth-child(3) {
				width: 45%;

				@media all and (max-width: $mobile) {
					width: 100%;
					padding-right: 0%;
				}
			}

			&:nth-child(4) {
				width: 18%;

				@media all and (max-width: $mobile) {
					width: 100%;
					padding-right: 0%;
				}
			}
		}

	}
}

.audience-table {
	margin-bottom: 70px;

	.tr {
		margin-bottom: 70px;

		@media all and (max-width: $mobile) {
			padding-top: 10px;
			margin-bottom: 40px;
		}

		.td {
			&:nth-child(1) {
				width: 32%;

				@media all and (max-width: $mobile) {
					width: 100%;
					padding-right: 0%;
				}
			}

			&:nth-child(2) {
				width: 68%;

				@media all and (max-width: $mobile) {
					padding-top: 20px;
					width: 100%;
					padding-right: 0%;
				}
			}


		}

	}

	.portrait {
		display: inline-block;
		vertical-align: top;
		width: 210px;
		border-radius: 50%;
		// margin-right: -25px;
		margin-bottom: -20px;
		overflow: hidden;
		-webkit-mask-image: -webkit-radial-gradient(white, black);

		@media all and (max-width: $mobile) {
			width: 170px;
		}

		img {
			display: block;
			width: 100%;
		}
	}
}


.character-table {

	// margin-bottom: 70px;
	.tr {
		margin-bottom: 50px;

		@media all and (max-width: $mobile) {
			padding-top: 10px;
			margin-bottom: 40px;
		}

		.td {
			&:nth-child(1) {
				width: 58%;

				@media all and (max-width: $mobile) {
					width: 100%;
					padding-right: 0%;
				}
			}

			&:nth-child(2) {
				width: 42%;

				@media all and (max-width: $mobile) {
					padding-top: 20px;
					width: 100%;
					padding-right: 0%;
				}
			}

			.pic {
				width: 210px;
				height: 210px;
