	&:hover {
		background-color: $less-transparent-orange;
	}
    
	@media all and (min-width: $mobile) {
		&.big {
			padding: 20px 20px;
			font-size: 20px;
			line-height: 16px;
	
			&::before {
				margin-right: 8px;
			}
		}
	}
}

.checklist {
	padding-left: 50px;
	margin-bottom: 40px;

	li {
		border: none;
		position: relative;
		padding: 10px 0;
		
		&::before {
			content: url("../assets/images/checkmark.svg");
			position: absolute;
			top: 15px;
			left: -50px;
			width: 35px;
			height: 30px;
		}

		p {
			width: 80%;
			margin: 0;
			@media all and (max-width: $mobile) {
				width: 100%;
			}
		}
	}

	@media all and (max-width: $mobile) {
		padding-left: 35px;
		margin-bottom: 20px;

		li::before {
			top: 13px;
			left: -35px;
			width: 25px;
		}
	}
}	


@import "node_modules/slick-carousel/slick/slick.scss";

.review-carousel {
  margin: 0 -60px 30px;

  @media all and (max-width: 1440px) {
    margin: 0 -35px 30px;
  }

  @media all and (max-width: $mobile) {
	  margin: 30px 0;
	}

  .slides-container {
    position: relative;
    width: 100%;

    @media all and (max-width: 1440px) {
      width: 100%;
    }

    @media all and (max-width: $mobile) {
      width: 100%;
      box-shadow: 0 0 60px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
    }

    ul {
      margin: 0;

      li {
        padding: 0;
        border: none;
      }
    }
    
    button.next-slide {
      cursor: pointer;
	  position: absolute;
	  transform-origin: center center;
	  transition: transform 200ms ease;
      padding: 15px;
      margin-left: 20px;
      bottom: 85px;

      width: calc(22em - 40px);
      font-size: 16px;
      font-family: inherit;
      color: white;
      border: none;
      border-radius: 4px;
      background: black;

      &:hover {
        opacity: .85;
      }
      
      &:active {
        transform: scaleX(0.99) scaleY(0.98);
      }

      @media all and (max-width: 1440px) {
        bottom: 60px;
      }
      
      @media all and (max-width: $mobile) {
        width: calc(100% - 20px);
        margin-left: 10px;
        bottom: 10px;
      }
    }
			
    .slick-arrow {
      cursor: pointer;
      position: absolute;
      z-index: 1;
      top: 50%;
      left: 60px;
      border: none;
      height: calc(100% - 80px);
      padding: 0 10px;
      background: transparent;
    transform: translateY(-50%);
    
    @media all and (max-width: 1440px) {
      left: 35px;
    }
	  
	  @media all and (max-width: $mobile) {
        left: 0;
      }

      svg {
        display: block;
        height: 16px;
        width: 9px;

        polyline {
          fill: none;
          stroke: #000;
          stroke-linecap: round;
          stroke-linejoin: round;
          stroke-width: 2px;
        }
      }

      &.slick-prev {
        padding: 0 30px 0 10px;
      }

      &.slick-next {
		transform: translateX(-100%) translateY(-50%) rotate(180deg);
		
		@media all and (max-width: $mobile) {
			right: 0;
			transform: translateX(0) translateY(-50%) rotate(180deg);
		  }
      }

      &:hover {
        svg {
          polyline {
            stroke: $orange;
    
            @media all and (max-width: $mobile) {
              stroke: black;
            }
          }
        }
      }

      &:active {
        svg {
          polyline {
            stroke: $orange;
          }
        }
      }
    }
    
    .slick-dots {
      display: flex;
      position: absolute;
      bottom: 50px;
      padding-left: 60px;
      width: calc(100% - 120px);

      @media all and (max-width: 1440px) {
        padding-left: 35px;
        bottom: 25px;
      }

      @media all and (max-width: $mobile) {
        padding: 0 10px;
        bottom: unset;
      }
    
      li {
		cursor: pointer;
        position: relative;
        background: lightgray;
        width: 100%;
        height: 2px;
        border-radius: 2px;
        margin-right: 10px;
        transition: background 250ms cubic-bezier(0.1,0,.25,1);

        @media all and (max-width: $mobile) {
          margin-right: 3px;
        }

        button {
          font-size: 0;
          padding: 0;
          border: none;
        }
      
        &:last-of-type {
          margin-right: 0;
        }
      
        &.slick-active {
          background:black;
        }
      
        &:before {
          content: '';
          position: absolute;
          height: 30px;
          top: -15px;
          left: 0;
          right: 0;
        }
      }
    }

    .slick-slide > div { // первый див внутри слик-слайд
      padding: 60px;

      @media all and (max-width: 1440px) {
        padding: 35px;
      }

      @media all and (max-width: $mobile) {
        padding: 0;
      }
    } 

    .slide {

      .wrapper {
        position: relative;
        display: inline-flex;
        flex-direction: row;
        box-shadow: 0 0 40px rgba(0, 0, 0, 0.15);
        border-radius: 4px;
        
        @media all and (max-width: $mobile) {
          display: flex;
          flex-direction: column;
          box-shadow: none;
          padding-bottom: 5em;
        }

        .image-container {
          position: relative;
          width: calc(100% - 22rem);
          
          @media all and (max-width: $mobile) {
            width: 100%;
          }
          
          img {
            display: block;
            width: auto;
            max-height: 90vh;
            max-width: 100%;
			      border-radius: 4px 0 0 4px;
          }
        }

        .caption {
          width: 22rem;
          padding: 10px 0 20px;
          font-size: 16px;
          line-height: 1.4;
          background-color: white;
          border-left: 1px solid rgba(0, 0, 0, 0.05);
          border-radius: 0 4px 4px 0;

          p {
            padding: 0 20px;
            width: auto;
          }

          @media all and (max-width: $mobile) {
            padding: 0;
            margin: 30px 0 0;
            font-size: 14px;
            width: 100%;
            height: auto;

            p {
              padding: 0 10px;

              &:first-of-type {
                margin: 0;
              }
            }
          }
        }
      }
    }
  }
}
