@import "mixins";

.enjoy-typeface {
    background-color: $orange;
    margin: 30px 0 60px;

    display: flex;

    .text-block {
        --text-wght: 300;
        --text-wdth: 390;

        color: white;
        font-family: $dodo-font;
        font-variation-settings: 'wght' var(--text-wght), 'wdth' var(--text-wdth);
        
        font-variant-caps: all-small-caps;
        font-size: 120px;
        line-height: 0.7;

        width: 2em;
        margin-left: 25%;
        margin-right: 60px;
        padding: 60px 0;

        span {
            font-variation-settings: 'wght' var(--text-wght), 'wdth' var(--text-wdth);
        }
    }

    .pizzer-image {
        display: block;
        width: 30em;
        max-height: 23em;

        img {
            display: block;
            height: 100%;
        }
    }

    @media all and (max-width: $mobile) {
        margin: 30px 0 40px;

        .text-block {
            font-size: 80px;
        }

        .pizzer-image {
            display: none;
        }
    }
}

.image {
    img {
        display: block;
        width: 100%;
    }
}

.typeface-spirit {
    width: 90%;
    margin: 30px 0 40px;

    @media all and (max-width: $mobile) {
        width: 100%;
        margin: -10px 0 30px;
    }
}

.alternate {
    --ss00-switch: off;
    --tnum-switch: off;
    --sups-switch: off;
    margin-bottom: 60px;

    .alt-features-preview {
        margin-top: 20px;
        font-weight: 300;
        font-size: 90px;
        line-height: 1;
        font-family: $dodo-font;
        font-feature-settings: "tnum" var(--tnum-switch), "ss01" var(--ss00-switch);

        span {
            font-feature-settings: "sups" var(--sups-switch);
        }
    }

    label {
        display: flex;
        align-items: center;
        cursor: pointer;
        font-size: 20px;
        
        .toggle-switch {
            cursor: pointer;
            -webkit-appearance: none; /* hide checkbox */
            outline-width: 0; /* remove chrome's blue outline */
            height: 1.5em;
            width: 3em;
            background: rgba(0, 0, 0, 0.2);
            margin-top: 5px;
            margin-left: 0.5em;
            border: none;
            border-radius: 1.5em;
            position: relative;
            transition: .2s;
                
            &:checked {
                background: $orange;
            }
                
            &::before {
                content: '';
                height: 1.1em;
                width: 1.1em;
                background: white;
                border-radius: 1.5em;
                position: absolute;
                top: 2px;
                left: 2px;
                transition: .15s;
            }
                
            &:checked:before {
                transform:translateX(1.5em);
            }
            
            &:active:before {
                background: #eee;
            }
        }
    }

    @media all and (max-width: $mobile) {
        margin-bottom: 40px;

        .alt-features-preview {
            font-size: 2.7em;
            margin-top: 10px;
        }

        label {
            font-size: 15px;
        }
    }
}

.multilanguage-title {
    position: relative;
    font-weight: 300;
    @include fluid-type(50px, 150px);
    line-height: 1;
    color: $orange;
    font-family: $dodo-font;

    margin-top: 50px;
    margin-bottom: 20px;

    @media all and (max-width: $mobile) {
        margin-top: 20px;
        margin-bottom: 10px;
    }
    
    .multilanguage-counter-label {
        line-height: 0.8;
        font-family: $main-font;
        position: absolute;
        top: -15%;
        right: 4%;

        color: white;
        text-align: center;
        transform: rotate(-15deg);
    
        .figure {
            font-size: 66px;
        }
    
        .caption {
            padding-top: 5px;
            font-size: 22px;
        }

        &::after {
            content: '';
            position: absolute;
            background: black;
            z-index: -1;
            top: -50%;
            left: -50%;
            transform: translate(11%, 7%);
            border-radius: 130px;
            width: 130px;
            height: 130px;
        }
    }
}


.multilanguage-desc {
    font-size: 16px;
    line-height: 1.4;
}

.multilanguage-slides  {
    margin: 25px 0 80px;

    @media all and (max-width: $mobile) {
        margin: 15px 0 40px; 
    }

    nav.language-tabs {
        display: flex;
        margin-left: -25px;
        overflow-X: unset;

        @media all and (max-width: $mobile) {
            padding-top: 10px;
            margin-left: -18px;
            // margin-right: -18px;
            overflow-X: scroll;
            -webkit-overflow-scrolling: touch;
        }
          
        .active {
            &::after {
                content: '';
                position: absolute;
                z-index: -1;
                top: -10px;
                left: 15px;
                right: 15px;
                bottom: 0;
                border-radius: 5px;
                background: $transparent-orange;

                @media all and (max-width: $mobile) {
                    top: -10px;
                    left: 10px;
                    right: 10px;
                    bottom: 4px;
                }
            }

            .tab .flag-pic img.stroke {
                border:none;
            }
        }

        .tab {
            position: relative;
            padding: 0 25px 10px;
            text-align: center;
            cursor: pointer;
    
            .flag-pic {
                height: 50px;
                width: 100%;
    
                img {
                    height: 100%;
    
                    &.stroke {
                        border: 1px solid rgba(0, 0, 0, 0.1);
                    }
                }
            }
    
            .caption {
                margin-top: 10px;
                font-size: 16px;
            }


            @media all and (max-width: $mobile) {
                padding: 0 18px 10px;

                .flag-pic {
                    height: 40px;
                }

                .caption {
                    margin-top: 5px;
                    font-size: 13px;
                }
            }

            @media all and (min-width: $mobile) {
                &:hover {
                    &::after {
                        content: '';
                        position: absolute;
                        z-index: -1;
                        top: -10px;
                        left: 15px;
                        right: 15px;
                        bottom: 0;
                        border-radius: 5px;
                        border: 2px solid $less-transparent-orange;
                    }
    
                }
            }
        }
    }

    p {
        display: none;
        opacity: 0;
        font-family: $dodo-font;
        font-weight: 300;
        line-height: 1.5;
        height: 120px;

        &.active {
            display: block;
            opacity: 1;
            transition: opacity 250ms ease;
        }

        @media all and (max-width: $mobile) {
            font-size: 16px;
            height: 18em;
        }
    }
}

.font-examples {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 0;
    overflow-X: unset;

    .image {
        width: calc(33% - 30px);
        margin-right: 30px;
        margin-bottom: 30px;

        img {
            width: 100%;
        }
    }

    @media all and (max-width: $mobile) {
        flex-wrap: nowrap;
        overflow-X: scroll;
        -webkit-overflow-scrolling: touch;
        padding: 0 18px;
        margin-left: -18px; 
        // margin-right: -18px;
        margin-bottom: 0;

        .image {
            flex-shrink: 0;
            height: 60vh;
            width: auto;
            margin-right: 20px;
            margin-bottom: 20px;

            img {
                height: 100%;
                width: auto;
            }
        }
    }
}