.mission {
  margin: .5em 0 1em;
  font-size: 4.6em;
  line-height: 1.2em;

  @media all and (max-width: $mobile) {
    font-size: 1.7em;
    line-height: 1.2em;
  }

  span {
    color: $orange;
  }
}


.examples {
  margin: .5em 0 1em;
  border-left: 1px solid rgba(0,0,0,.5);
  padding-left: 1em;
}

.marker {
  display: inline-block;
  // background-color: rgba(255,235,0,1);
  background-color: $orange;
  color: white;

  font-size: 1.3em;
  padding: .5em .8em;
  border-radius: 0 .8em .8em .8em;
  // margin: .05em 0;
}

.columns {
  margin: 1em 0 2em;
  display: flex;
  width: 80%;

  @media all and (max-width: $mobile) {
    width: 100%;
  }

  p {
    width: 100%;
  }

  .column {
    width: 50%;
    padding-right: 4%;
  }
}

.formula {
  margin: 2em 0;
  
  .formula-element {
    display: inline-block;

    padding: .2em .5em .3em;
    margin: .3em .3em .3em 0;
    
    font-size: 1.5em;
    color: $orange;

    background-color: $supertransparent-orange;
    border-radius: .2em;

    &.no-border {
      background-color: transparent;
      border: none;
      padding: .2em 0;
    }

  }
}

.paper-container {
  width: 80%;

  @media all and (max-width: $mobile) {
    width: 100%;
  }
}

.paper {
  margin: 3em auto;
  width: 30em;
  box-shadow: 0 0 30px rgba(0,0,0,0.1);

  @media all and (max-width: $mobile) {
    width: 100%;
  }

  img {
    width: 100%;
  }

  .stamp {

    &.smaller-font {
      font-size: .8em;
      line-height: 1.3em;

    }

    .question {
      margin-top: -1px;
      border: 1px solid black;
      padding: .6em;
    }

    .top {
      margin-bottom: 1.2em;
    }

    .bottom {
      margin-top: 2em;
    }
  }

  span {
    opacity: .4;
  }

  .line {
    border-bottom: 1px dotted gray;
    margin: 1em 0 .8em;
  }

}