$concept-image-width: 50%;

.facade-types {
    // margin-right: -10%;

    @media all and (max-width: $mobile) {
        margin-right: 0;
    }

    h4 {
        margin-bottom: 0;
    }
}

.type-gallery {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 60px;

    @media all and (max-width: $mobile) {
        margin-bottom: 30px;
    }
}

.switching-link, .switching-link-2 {
    border-bottom-style: dotted;

    &.active {
        border: none;
        background-color: $less-transparent-orange;
    }
}

.type-gallery-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    font-size: 20px;

    @media all and (max-width: 1280px) {
        font-size: 18px;
    }
    
    .type-gallery {
        margin: 2em 0;
        display: flex;
        position: relative;
        max-width: 70%;
        height: 20em;
    
        .facade-type {
            position: absolute;
            width: auto;
            padding: 1em;
            background-color: white;
            box-shadow: 0 0 3px rgba(0,0,0,0.10);
            cursor: pointer;
    
            z-index: auto;
            transition: z-index 300ms ease;
    
            transition: bottom 200ms ease;
            bottom: 0;
    
            &:hover {
                bottom: 10px;
            }
    
            &.animation-down-up {
                animation: down-up 400ms ease-in-out 1;
                
            }
    
            @keyframes down-up {
                0% {
                    bottom: 10px;
                }
                50%{
                    bottom: 30px;
                }
                100% {
                    bottom: 10px;
                }
            }
    
            img {
                display: block;
                width: 15em;
                height: 15em;
            }
    
            p {
                margin-bottom: 1em;
            }
        }
    }

        
    @media all and (max-width: $mobile) {
        flex-direction: column;
        font-size: 10px;
        
        .type-gallery {
            margin: 2em 0 0;
            height: 22em;
            
            .facade-type {
                img {
                    width: 14em;
                    height: 14em;
                }
        
                p {
                    font-size: 16px;
                }
            }
            
        }
    }
}

.facade-element {
    display: flex;
    margin-top: 40px;
    // margin-right: -7%;

    .image-container {
        order: 1;
        width: 66.66%;

        img {
            display: block;
            width: 100%;
            height: 100%;
        }

        .facede-element-image {
            display: flex;
            overflow: hidden;
            margin-bottom: 20px;
        }
    }

    .caption {
        order: 2;
        padding-left: 30px;
        padding-right: 0;
        width: 33.33%;

        h4 {
            margin-top: 0;
        }

        p {
            font-size: 16px;
            line-height: 1.375;
        }
    }

    // .fotorama__nav {
    //     text-align: left;
    // }

    .fotorama__thumb-border {
        border-color: #ff6900;
    }

    @media all and (max-width: $mobile) {
        margin-right: 0;
        flex-direction: column;

        .image-container { width: 100% }

        .caption {
            padding: 0;
            width: 100%;
            
            h5 {
                margin-top: 20px;
                margin-bottom: 0;
            }
        }

    }
}

.flip {
    .image-container { order: 2; }
    .caption {
        order: 1;
        padding-left: 0;
        padding-right: 30px; 
    }

    @media all and (max-width: $mobile) {
        .image-container { order: 1; }
        .caption { 
            order: 2;
            padding: 0;
        }
    }
}

// Interiors

.format {
    margin-top: 40px;

    img {
        width: 100%;
    }

    .fotorama {
        margin-top: 20px;
    }
}


.design-concept-nav {
    display: flex;
    margin-top: 30px;

    .section-switch {
        width: 33%;
        padding: 10px;
        color: $orange;
        text-decoration: none;
        text-align: center;
        border: none;
        background-color: $transparent-orange;

        &:first-of-type {
            border-radius: 5px 0 0 5px;
        }

        &:last-of-type {
            border-radius: 0 5px 5px 0;
        }

        &:hover {
            background-color: $less-transparent-orange;
        }

        img {
            border-radius: 4px;
            display: block;
            width: 100%;
            height: auto;
            margin-bottom: 10px;
        }
    }

    .selected {
        background-color: $less-transparent-orange;
    }
}

.design-concept {
    margin-bottom: 60px;

    // &::before {
    //     content: "";
    //     display: block;
    //     position: relative;
    //     width: 100%;
    //     height: 60px; 
    //   }

    h4 { 
        font-size: 50px;
        font-weight: normal;
        color: $orange;
    }

    .design-concept-section {
        margin-top: 40px;

        .flex-wrapper {
            display: flex;
            flex-wrap: wrap;
            // margin-right: -7%;

            @media all and (max-width: $mobile) {
                flex-direction: row;
                margin: 0;
            }
        }
    }

    .design-concept-description {
        display: flex;
        // margin-right: -7%;

        .design-concept-image {
            position: relative;
            width: $concept-image-width;
            height: 0;
            overflow: hidden;
            border-radius: 4px;
            
            padding-bottom: $concept-image-width*3/4;
            flex-shrink: 0;
        
            img {
                display: block;
                width: 115%;
                height: auto;
                position: absolute;
                top: -50%;
                left: -50%;
                right: -50%;
                bottom: -50%;
                margin: auto;
            }
        }

        .design-concept-image-caption {
            width: 50%;
            padding-right: 30px;
        }

        @media all and (max-width: $mobile) {
            flex-direction: column;
            margin: 0;

            .design-concept-image {

            }

            .design-concept-image-caption {
                width: 100%;
                padding-right: 0;
            }
        }
    }

    .example {
        display: flex;
        width: 33.33%;
        box-sizing: border-box;
        margin-top: 25px;
        margin-bottom: 0;
        padding-right: 30px;

        font-size: 16px;
        line-height: 1.375;

        .example-image {
            position: relative;
            width: 33%;
            height: 0;
            overflow: hidden;
            border-radius: 4px;
            
            padding-bottom: 33%;
            flex-shrink: 0;
        
            img {
                display: block;
                width: 100%;
                height: auto;
                position: absolute;
                top: -50%;
                left: -50%;
                right: -50%;
                bottom: -50%;
                margin: auto;
            }
        }

        .example-caption {
            padding-left: 20px;

            p {margin-top: 0;}
        }

        @media all and (max-width: $mobile) {
            width: 100%;
            padding: 0;

            .example-caption {
                padding-left: 0;
                padding-right: 20px;

                p {margin-top: 10px;}
            }
        }

    }

    .vertical {
        flex-direction: column;
        width: 25%;
        padding-right: 20px;

        @media all and (max-width: $mobile) {
            width: 100%;
        }

        .example-image {
            width: 100%;
            padding-bottom: 100%;
        }

        .example-caption {
            padding-left: 0;

            p {margin-top: 10px;}
        }
    }

    .vertical-3-2-ratio {
        flex-direction: column;
        width: 50%;
        padding-right: 20px;

        .double {
            width: 50%;
            padding-right: 20px;
            box-sizing: border-box;
        }
        
        .example-image {
            width: 100%;
            padding-bottom: calc(100% * 2 / 3);
        }

        .example-caption {
            padding-left: 0;
            
            p {margin-top: 10px;}
        }
    }
}

.inner-section {
    margin: 30px 0;
}