@charset "utf-8";
@import "mixins";

$main-font: 'PT_Root_UI_Regular',Helvetica,sistem-ui,sans-serif;

$dodo-font: 'Dodo-Rounded', 'PT_Root_UI_Regular',Helvetica,sistem-ui,sans-serif;

@include font-face('PT_Root_UI_Regular');
@include font-face('PT_Root_UI_Bold', bold, normal);
@include font-face('PT_Root_UI_Light', thin, normal);
// @include font-face('PT_Root_UI_Bold', 900, normal);
@font-face {
    font-family: 'Dodo-Rounded';
    src: url('../assets/fonts/DoDo-VF.ttf') format('truetype');
    src: url('../assets/fonts/DoDo-VF.woff2') format('woff2-variations');
    font-weight: 100 700;
    font-stretch: 0% 300%;
    font-style: oblique 0deg 20deg;
}

/*  V A R I A B L E S  */

$text-colour: rgba(20, 20, 20, 1);
$text-underline-colour: rgba(20, 20, 20, .3);

$link-colour: rgba(20, 20, 20, 1);
$link-underline-colour: rgba(20, 20, 20, .3);

$link-hover-colour: rgba(255, 105, 0, 1);
$link-hover-underline-colour: rgba(255, 105, 0, 0.3);

$orange: rgba(255, 105, 0, 1);
$supertransparent-orange: rgba(255, 105, 0, .07);
$transparent-orange: rgba(255, 105, 0, .1);
$less-transparent-orange: rgba(255, 105, 0, .2);
$half-transparent-orange: rgba(255, 105, 0, .4);

$base-font-size: 20px;
$base-line-height: 26px;

$mobile: 840px;


/*  B A S I C S  */

body {
	margin: 0;
	font-family: $main-font;
	color: $text-colour;

	font-size: $base-font-size;
	line-height: $base-line-height;
	font-weight: 400;

	min-width: 768px;

	@media all and (max-width: $mobile) {
		min-width: auto;
		font-size: 15px;
		line-height: 20px;
	}

}


a {
	color: $link-colour;
	text-decoration: none;
	border-bottom: 1px solid $link-underline-colour;

	&:hover {
		color: $link-hover-colour;
		border-bottom-color: $link-hover-underline-colour;
	}
}

p {
	margin-top: 10px;
	margin-bottom: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: normal;
	margin-top: 0;
	margin-bottom: 0;
}


h1 {
	margin-top: 20px;
	font-size: 135px;
	line-height: 125px;
	margin-bottom: 50px;
	color: $orange;

	@media screen and (max-width: 1150px) {
		font-size: 125px;
		line-height: 115px;
	}

	@media screen and (max-width: 1050px) {
		font-size: 115px;
		line-height: 110px;
	}

	@media screen and (max-width: 970px) {
		font-size: 105px;
		line-height: 100px;
	}

	@media screen and (max-width: 890px) {
		font-size: 95px;
		line-height: 90px;
	}

	@media all and (max-width: $mobile) {
		font-size: 40px;
		line-height: 40px;
		margin-bottom: 15px;
	}
}

h2 {
	margin-top: 1em;
	font-size: 3em;
	line-height: 1.2em;
	margin-bottom: .5em;

	@media all and (max-width: $mobile) {
		
		font-size: 32px;
		line-height: 40px;
		margin-bottom: 10px;
	}
}

h3 {
	margin: 2em 0 .5em;
	font-size: 1.8em;
	line-height: 1.3em;
	font-family: 'PT_Root_UI_Bold',Helvetica,Arial,sans-serif;
	font-weight: 900;
	// color: $orange;

	@media all and (max-width: $mobile) {
		font-size: 24px;
		line-height: 36px;
	}
}

h4 {
	margin: 40px 0 20px;
	font-size: 20px;
	font-family: 'PT_Root_UI_Bold',Helvetica,Arial,sans-serif;
	font-weight: 900;

	@media all and (max-width: $mobile) {
		font-size: 16px;
	}
}


h5 {
	margin-top: 20px;
	font-size: $base-font-size;
	line-height: $base-line-height;
	text-transform: uppercase;
	letter-spacing: 3px;

	&.top-margin-10 {

		margin-top: 10px;
		@media all and (max-width: $mobile) {
			margin-top: 20px;
		}
	}
}

h6 {
	margin-top: 20px;
	font-size: 14px;
	line-height: 15px;
	letter-spacing: 2px;
	text-transform: uppercase;
}

.mobile {
	margin-top: 20px;
	margin-bottom: 10px;
	display: none;
	@media all and (max-width: $mobile) {
		display: block;
	}
}

strong {
	font-family: 'PT_Root_UI_Bold',Helvetica,Arial,sans-serif;
	font-weight: 900;
}

u {
	 text-decoration: none;
	 border-bottom: 5px solid $orange;
}

.lead {
	font-size: 1.2em;
	line-height: 1.4em;

	@media all and (max-width: $mobile) {
		font-size: 20px;
		line-height: 25px;
	}
}

//- TODO: исправить обертку в article/navigo
article, .article {
	border-radius: 8px;
	margin-bottom: 50px;
	padding-bottom: 3em;

	@media all and (max-width: $mobile) {

		border: none;
		box-shadow: none;
		margin-bottom: 50px;
		padding: 0;

	}

	& > p {
		width: 80%;

		@media all and (max-width: $mobile) {
			width: 100%;
		}

	}

	
}

.section {
	margin: 4% auto;
	width: 94%;
	max-width: 1300px;
	// background-color: rgba(255,255,0,.2);

	@media all and (max-width: $mobile) {
		margin: 18px auto;
		width: 90%;
	}
}



textarea:focus,
input:focus,
select:focus,
button {
	outline: none;
}
