@import "mixins";

$example-pic-width: 30%;
$example-pic-width-mobile: 75%;

#values {
    // padding: 3%;
    
    @media all and (max-width: $mobile) {
        padding: 0;
    } 
} //пока так, чтобы всё не ломать

.introduction {
    @media all and (min-width: $mobile) { //с min-width, чтобы только на десктопе
		p {
            width: 90%;
            font-size: 20px;
            line-height: 26px;
        }
	}
}


// Буквенные секции

.value-section-container {
    display: flex;
    flex-wrap: wrap;

    &::before {
        content: "";
        display: block;
        position: relative;
        width: 100%;
        height: 60px; 
      }
    
    @media all and (min-width: $mobile) { //с min-width, чтобы только на десктопе
        font-size: 16px;
        line-height: 22px;

        &::before {
            content: "";
            display: block;
            position: relative;
            width: 100%;
            height: 80px; 
          }
    }

    h5 {
        margin-bottom: 20px;
    }

    h6 {
        color: rgba(0, 0, 0, 0.6);
        margin-top: 50px;
        margin-bottom: 20px;

        @media all and (max-width: $mobile) {
            margin-top: 20px;
            margin-bottom: 0;
        }
    }
    

}


.value-section-header {
    display: flex;
    align-items: center;

    @media all and (max-width: $mobile) {
        margin-left: -3px;
    }

    h3 {
        margin: 0 !important;
        @include fluid-type(100px, 170px);
        line-height: .75;
        color: transparent;
        font-family: 'PT_Root_UI_Bold',Helvetica,Arial,sans-serif;
        font-weight: 900;
        -webkit-text-stroke: 2px #ff6900;

        @media all and (max-width: $mobile) {
            font-size: 55px;
            line-height: 1;
            -webkit-text-stroke: 1px #ff6900;
        }
    }
}

.value-section-content {
    flex-basis: 100%;
    flex-grow: 1;
    // margin-top: 20px;
    padding-right: 0;

    @media all and (max-width: $mobile) {
        margin-top: 0;
        margin-bottom: 20px;

        h3 {
           margin: 10px 0 10px; 
        }
    } 

}

.flex-wrapper {
    display: flex;
    
    @media all and (max-width: $mobile) {
        flex-wrap: wrap;
        flex-direction: column;
    }    
}


// Преимущества (как проявляется)

.example-container {
    display: flex;
    flex-wrap: wrap;

    @media all and (max-width: $mobile) {
        margin-top: 20px;
    }

    .example:last-child {
        margin-bottom: 0;
    }
}

.example {
    width: 50%;
    display: flex; 
    flex-direction: row;
    margin-bottom: 30px;

    @media all and (max-width: $mobile) {
        width: 100%;
        flex-direction: column;
    }

}


.example-picture {
    position: relative;
    width: $example-pic-width;
    height: 0;
    overflow: hidden;
    border-radius: 4px;
    
    padding-bottom: $example-pic-width*2/3;
    margin-right: 30px;
    flex-shrink: 0;

    @media all and (max-width: $mobile) {
        width: $example-pic-width-mobile;
        height: auto;
        padding-bottom: $example-pic-width-mobile*2/3;
        margin-right: 0;
    }

    img {
        display: block;
        width: 100%;
        height: auto;
        position: absolute;
        top: -50%;
        left: -50%;
        right: -50%;
        bottom: -50%;
        margin: auto;
    }
}

.example-description {
    flex-direction: column;

    h5 {
        margin-bottom: 0;
        margin-top: 0;

        @media all and (max-width: $mobile) {
            margin-top: 10px;
        }
    }
    
    p {
        margin-right: 40px;
        
        &:first-child {
            margin-top: 0;
        }

        @media all and (max-width: $mobile) {
            margin-right: 0;
            
            &:first-child {
                margin-top: 10px;
            }
        }
    }
}

.example-column {
    width: 33%;
    flex-direction: column;
    margin-bottom: 30px;

    @media all and (max-width: $mobile) {
        width: auto;

        &:last-child {
            margin-bottom: 0;
        }
    }

    .example-column-wrapper {
        margin-right: 40px;

        @media all and (max-width: $mobile) {
            margin-right: 0;
        }
    }

    h5 {
        margin-top: 15px;
        margin-bottom: 0;
    }

    img {
        display: block;
        width: 100%;
        height: auto;
        border-radius: 4px;
    }

}


.value-section-nav {
    display: flex;
    flex-direction: column;
    margin-right: 20px;

    @media all and (max-width: $mobile) {
        flex-direction: row;
        margin-bottom: 15px;
    }

    a {
        padding: 3px 8px;
        font-size: 18px;
        line-height: 1;
        text-align: center;
        color: $orange;
        text-decoration: none;
        border: none;
        background-color: $transparent-orange;
        
        &:first-child {
            padding: 4px 8px 3px;
            border-radius: 4px 4px 0 0;
        }

        &:last-child {
            padding: 3px 8px 4px;
            border-radius: 0 0 4px 4px;
        }

        &:hover {
            background-color: $less-transparent-orange;
        }

        @media all and (max-width: $mobile) {
            padding: 4px 8px;

            &:first-child {
                padding: 4px 8px;
                border-radius: 4px 0 0 4px;
            }
    
            &:last-child {
                padding: 4px 8px;
                border-radius: 0 4px 4px 0;
            }
        }
    }

    .selected {
        background-color: $less-transparent-orange;
    }

}