.legacy-header-container {
	position: relative;
	z-index: 10;
	height: 80px;

	@media all and (max-width: $mobile) {
		height: 80px;
	}

	.header {
		position: fixed;
		display: flex;

		top: 0;
		left: 0;
		right: 0;

		transition: all 0.3s ease;
		background-color: white;
		padding: 20px 25px 25px 180px;
		box-shadow:  0 0 60px rgba(0,0,0,0.15);

		@media all and (max-width: $mobile) {
			padding: 10px 10px 12px 50px;
		}

		&.stuck {

        }
        
        .logo {
            position: absolute;
            top: 20px;
            left: 25px;
            width: 120px;
        
            @media all and (max-width: $mobile) {
                top: 10px;
                left: 10px;
                width: 28px;
            }
        
            .desktop-logo-white {
                display: none;
            }
        
            .desktop-logo {
                @media all and (max-width: $mobile) {
                    display: none;
                }
            }
        
            .mobile-logo {
                display: none;
                @media all and (max-width: $mobile) {
                    display: block;
                }
            }
        
            img {
                display: block;
                width: 100%;
            }
        
            a {
                border-bottom: 0;
        
                &:hover {
                    opacity: .9;
                }
            }
        }
        
        .language-switcher {
            position: absolute;
            top: 20px;
            right: 25px;
        
            @media all and (max-width: $mobile) {
                top: 10px;
                right: 10px;
                transform-origin: top right;
                transform: scale(.8);
            }
        
            .links {
                padding: 0;
        
                @media all and (max-width: $mobile) {
                    border: 1px solid rgba(0, 0, 0, 0.2);
                    border-radius: 4px;
                }
        
                a,
                span {
                    &:first-child {
                        padding-left: 12px;
                        @media all and (max-width: $mobile) {
                            padding-left: 12px;
                        }
                    }
        
                    &:last-child {
                        padding-right: 12px;
                        @media all and (max-width: $mobile) {
                            padding-right: 12px;
                        }
        
                    }
                }
        
        
            }
        
        
            span {
                color: gray;
            }
        
        
        }
        
	}

	&.dark {
		height: 0;

		.header {
			background-color: black;

			.logo {
				.desktop-logo {
					display: none;
				}

				.desktop-logo-white {
					display: block;
					@media all and (max-width: $mobile) {
						display: none;
					}
				}
			}

			.legacy-tags-navigation {
				a, span {
					color: white;
					background-color: transparent;
					padding: 9px 9px 8px 9px;
					border: 2px solid $half-transparent-orange;

					@media all and (max-width: $mobile) {
						padding: 4px 9px 3px 9px;
					}

					&:first-child {
						border-right: none;
						padding-left: 16px;
					}

					&:last-child {
						border-left: none;
						padding-right: 16px;
					}



					&:hover {
						color: $orange;
					}
				}
			}

			.language-switcher {


				.links {
					padding: 0;
					border: 2px solid rgba(255, 255, 255, 0.2);

					a {
						color: white;

						&:hover {
							color: $orange;
						}
					}


					a,
					span {
						&:first-child {
							padding-left: 12px;
							@media all and (max-width: $mobile) {
								padding-left: 12px;
							}
						}

						&:last-child {
							padding-right: 12px;
							@media all and (max-width: $mobile) {
								padding-right: 12px;
							}

						}
					}


				}


				span {
					color: gray;
				}


			}


		}
	}
}



.legacy-tags-navigation {
	margin-right: 20px;

	a, span {
		display: inline-block;
		vertical-align: top;
		padding: 10px 9px 9px 9px;
		box-sizing: border-box;
		border: 1px solid rgba(0, 0, 0, 0);

		color: $orange;
		background-color: $supertransparent-orange;

		font-size: 16px;
		line-height: 19px;


		@media all and (max-width: $mobile) {
			font-size: .8em;
            padding: 3px 9px 4px 9px;
		}

		&:first-child {
			border-radius: 4px 0 0 4px;
			padding-left: 16px;
		}

		&:last-child {
			border-radius: 0 4px 4px 0;
			padding-right: 16px;
		}

		&:hover {
			background-color: $transparent-orange;
		}

		&.active {
			background-color: $less-transparent-orange;
		}

	}

}

section.all {
	border: 1px solid rgba(0,0,0,0.1);
	// box-shadow: 0 0 60px  rgba(0,0,0,0.1);
	border-radius: 8px;
	margin-bottom: 50px;
    padding: 3% 10% 3% 3%;
    
    &:first-of-type {margin-bottom: 50px;}

	@media all and (max-width: $mobile) {

		border: none;
		box-shadow: none;
		margin-bottom: 50px;
		padding: 0;

	}

	
}